import { ReactNode } from "react";
import { Fields } from "./forms";



const MainInput = ({
  type,
  name,
  className,
  placeholder,
  autoComplete
}: Fields): ReactNode => {
  return (
    <input
      type={type}
      name={name ?? type}
      placeholder={placeholder}
      autoComplete={autoComplete}
      className={className}
    />
  )
}

export { MainInput }
