import { ReactNode } from "react"

type Props = {
    children?:ReactNode;
    className?:string;
}


const Popup = (
    {
        className,
        children
    }:Props
) => {
    return(
        <div className=
        {`
            bg-dark-50
            max-md:max-w-2xs
            min-md:max-w-xl
            w-full
            h-64
            my-32
            rounded-2xl
            backdrop-blur-2xl
            border-white/50
            border-1
            flex
            justify-center
            ${className}
        `}>
            {children}
        </div>
    )
}



export {Popup}
